  mounted(){
    let _this = this; // forEachthisthis
    let dragX;
    let dragY;
    let smZIndexCnt = 1;
    let scrollTop;
    let scrollLeft;
    let clickTop;
    let clickLeft;
    let downTime = performance.now(); // 
    let tooltipElem;          // 
    let tooltipNikkeId = "";  // ID
    let tooltipDispTime = performance.now();  // 

    // 
    if (window.innerWidth <= 520) {
      _this.coMenuOpen = false;
      //_this.mMenu('bsMenu', 'bsMenuBtn');
      _this.mMenu('sfMenu', 'sfMenuBtn');
      _this.mMenu('smMenu', 'smMenuBtn');
      //_this.mMenu('lsMenu', 'lsMenuBtn');
      //_this.mMenu('fsMenu', 'fsMenuBtn');
      _this.mMenu('tsMenu', 'tsMenuBtn');
    };

    // 
    for (let i = 0; i <= _this.damageListMaxNum - 1; ++i) {
      let newDamage = JSON.parse(JSON.stringify(_this.fsDamageListProt));
      newDamage.id = i;
      newDamage.disp = (i == 0) ? true : false;
      newDamage.order = i + 1;
      for (let i = 0; i <= 3; ++i) {
        let newSupporter = JSON.parse(JSON.stringify(_this.fsSupporterProt));
        newSupporter.id = i;
        newSupporter.order = i + 1;
        newDamage.supporters.push(newSupporter);
      }
      _this.damageList.push(newDamage);
    }

    function mdown(e, targetNode) {
      // .dragTag 
      targetNode.classList.add("dragTag");
      // 
      if(e.type === "mousedown") var event = e;
      else var event = e.changedTouches[0];
      // 
      dragX = event.pageX - targetNode.offsetLeft;
      dragY = event.pageY - targetNode.offsetTop;
      // 
      let parentElement = document.getElementById(targetNode.parentNode.id); 
      scrollTop  = parentElement.scrollTop;
      scrollLeft = parentElement.scrollLeft;
      // 
      let client_rect = targetNode.getBoundingClientRect();
	    clickTop = event.pageY - (client_rect.top + window.pageYOffset);
	    clickLeft = event.pageX - (client_rect.left + window.pageXOffset);
      // 
      downTime = performance.now();
    };

    function mmove(e) {
      // 
      removeToolTip();
      // 
      e.preventDefault();
      // 
      let drag = document.getElementsByClassName("dragTag")[0];
      if (drag == undefined) return;
      // 
      if(e.type === "mousemove") var event = e;
      else var event = e.changedTouches[0];
      // 
      drag.style.position = "absolute";
      drag.style.top = event.pageY - scrollTop - dragY + "px";
      drag.style.left = event.pageX - scrollLeft - dragX + "px";
      drag.style.zIndex = 9999;
    };

    function mup(e) {
      // 
      //removeToolTip();

      let drag = document.getElementsByClassName("dragTag")[0];
      if (drag == undefined) return;

      //  .dragTagtooltip 
      while (document.getElementsByClassName("dragTag")[0] != undefined) {
        let element = document.getElementsByClassName("dragTag")[0];
        element.classList.remove("dragTag");
      };

      //
      drag.style.display = "none";
      if (e.type == "mouseup" || e.type == "mouseleave") {
        var x = e.clientX;
        var y = e.clientY;
      } else {
        let t = e.changedTouches[0];
        var x = t.clientX;
        var y = t.clientY;
      }
      let dropPoint = document.elementFromPoint(x, y);
      drag.style.display = "";

      //
      if (dropPoint == undefined) {
        var drop = undefined;
      } else if (dropPoint.id.includes("fsImgAttacker")) {
        var drop = dropPoint.parentNode.parentNode.parentNode;
      } else if (dropPoint.id.includes("fsImgSupporter")) {
        var drop = dropPoint.parentNode.parentNode.parentNode.parentNode;
      } else if (dropPoint.id.includes("fsMarkBurstAtk")) {
        var drop = dropPoint.parentNode.parentNode.parentNode.parentNode;
      } else if (dropPoint.id.includes("fsMarkBurstSup")) {
        var drop = dropPoint.parentNode.parentNode.parentNode.parentNode.parentNode;
      } else if (dropPoint.id.includes("fsMsgAttacker")) {
        var drop = dropPoint.parentNode;
      } else if (dropPoint.id.includes("fsMsgSupporter")) {
        var drop = dropPoint.parentNode;
      } else if (dropPoint.id.includes("nikke")) {
        var drop = dropPoint.parentNode;
      } else if (dropPoint.id.includes("mark")) {
        var drop = dropPoint.parentNode.parentNode;
      } else {
        var drop = dropPoint;
      }

      if (drop == undefined) { // ()
        drag.style.position = "relative";
        drag.style.top = "0px";
        drag.style.left = "0px";
        drag.style.zIndex = 1;
      } else if (drop.id.includes("sfSquad")) { // 16
        let idxTo = drop.childNodes.length; // 
        let idxFrom = 0; // 
        let cnt = 0;
        // 
        drop.childNodes.forEach(function(item){
          if(item.id == dropPoint.id) idxTo = cnt;
          cnt++;
        });
        // 
        cnt = 0;
        if(drop.id == drag.parentNode.id) {
          drop.childNodes.forEach(function(item){
            if(item.id == drag.id) idxFrom = cnt;
            cnt++;
          });
          if(idxFrom < idxTo) { // <
            idxTo++;
          }
          if(performance.now() - downTime < 200) { //
            idxTo = idxFrom;
          }
        }
        
        drop.insertBefore(drag, drop.childNodes.item(idxTo)); // 
        drag.style.position = "relative";
        drag.style.top = "0px";
        drag.style.left = "0px";
        drag.style.zIndex = 1;

        _this.mSaveQf(_this.sqSquadSelect);
      } else if (drop.id.includes("Candidate")) { // ""
        let idx = 0;
        let cnt = 0;
        drop.childNodes.forEach(function(item){
          if(item.id != undefined && item.id.includes('nikke')) { // nikke
            if(item.id <= drag.id) idx = cnt+1;
            cnt++;
          }
        });
        drop.insertBefore(drag, drop.childNodes.item(idx));
        drag.style.position = "relative";
        drag.style.top = "0px";
        drag.style.left = "0px";
        drag.style.zIndex = 1;

        if (drop.id == "sfCandidate") _this.mSaveQf(_this.sqSquadSelect);

      } else if (drop.id == "smGraph") { // ""
        let dropRect = document.getElementById(drop.id).getBoundingClientRect(); // 
        //let dragRect = document.getElementById(drag.id).getBoundingClientRect(); // 
        drop.appendChild(drag);
        drag.style.position = "absolute";
        //drag.style.top = y - dropRect.top - dragRect.height/2 + "px";
        //drag.style.left = x - dropRect.left - dragRect.width/2 + "px";
        drag.style.top = y - dropRect.top - clickTop - 2 + "px";
        drag.style.left = x - dropRect.left - clickLeft - 2 + "px";
        drag.style.zIndex = ++smZIndexCnt;

      } else { // 
        drag.style.position = "relative";
        drag.style.top = "0px";
        drag.style.left = "0px";
        drag.style.zIndex = 1;
      };

      setCandidateHeight(); // 
    };

    // 
    function createTooltip(nikkeId, message, size) { // 
      removeToolTip();
      tooltipElem = document.createElement('div');
      tooltipElem.innerHTML = message;
      tooltipElem.className = "tooltipX"
      tooltipElem.style.fontSize = size + "em";
      //tooltipElem.style.visibility = "visible";
      document.body.append(tooltipElem);
      tooltipNikkeId = nikkeId;
    };
    function setTop(buttonRect, tooltipRect) { // 
      if (tooltipRect.height >= buttonRect.top) {
        tooltipElem.style.top = `${buttonRect.bottom}px`
        return buttonRect.bottom + tooltipRect.height;
      } else {
        tooltipElem.style.top = `${buttonRect.top - tooltipRect.height}px`
        return tooltipRect.height;
      }
    };
    function setLeft(buttonRect, buttonBaseDiff) {
      if (buttonRect.left + buttonBaseDiff < 0) {
        tooltipElem.style.left = "0px";
        return;
      }
      if (buttonRect.right - buttonBaseDiff > document.documentElement.clientWidth) {
        tooltipElem.style.right = "0px";
        return;
      }
      tooltipElem.style.left = `${buttonRect.left + buttonBaseDiff}px`;
    };
    function setTopHorizontal(buttonRect, tooltipRect, buttonBaseDiff) {
      if (buttonRect.top + buttonBaseDiff < 0) {
        tooltipElem.style.top = "0px";
        return;
      }
      if (buttonRect.bottom - buttonBaseDiff > document.documentElement.clientHeight) {
        tooltipElem.style.top = `${window.innerHeight - tooltipRect.height}px`;
        return;
      }
      tooltipElem.style.top = `${buttonRect.top + buttonBaseDiff}px`;
    };
    function setLeftHorizontal(buttonRect, tooltipRect) {
      if ((document.documentElement.clientWidth/2) > (buttonRect.left + buttonRect.width/2)) {
        tooltipElem.style.left = `${buttonRect.right}px`
        return buttonRect.right + tooltipRect.width;
      } else {
        tooltipElem.style.left = `${buttonRect.left - tooltipRect.width}px`
        return tooltipRect.width;
      }
    };
    function removeToolTip() { // 
      let ret = false;
      if (tooltipElem) {
        let elements = document.getElementsByClassName("tooltipX");
        for (let i=0; i<elements.length; ++i) {
          elements[i].remove();
          ret = true;
        };
        tooltipElem = null;
      };
      tooltipNikkeId = "";
      return ret;
    };
    async function tooltipStart(ev, flgTouch) { // 
      const overElement = ev.target;
      await wait(700);
      if (flgTouch == false && document.getElementsByClassName("dragTag")[0] != undefined) {
        tooltipNikkeId = "";
        return;
      };
      if (_this.coTouchDevice && tooltipDispTime > performance.now() - 500) {
        return;
      };
      if (overElement.id == tooltipNikkeId) {
        for (let i=80; i>=60; i-=5) {
          if (overElement.dataset.tooltip != undefined) {
            if (window.innerWidth > 520) { // 520px
              createTooltip(overElement.id, overElement.dataset.tooltip, i/100);
              const buttonRect = overElement.getBoundingClientRect();
              const tooltipRect = tooltipElem.getBoundingClientRect();
              if (document.documentElement.clientHeight > document.documentElement.clientWidth) {
                const buttonBaseDiff = (buttonRect.width - tooltipRect.width) / 2;
                let height = setTop(buttonRect, tooltipRect);
                setLeft(buttonRect, buttonBaseDiff);
                if (height < window.innerHeight) {
                  break;
                };
              } else {
                const buttonBaseDiff = (buttonRect.height - tooltipRect.height) / 2;
                let width = setLeftHorizontal(buttonRect, tooltipRect);
                setTopHorizontal(buttonRect, tooltipRect, buttonBaseDiff);
                if (width < window.innerWidth) {
                  break;
                };
              };
            } else { // 520px
              createTooltip(overElement.id, overElement.dataset.tooltip, i/100);
              const tooltipRect = tooltipElem.getBoundingClientRect();
              let top = window.innerHeight/2 - tooltipRect.height/2;
              let left = window.innerWidth/2 - tooltipRect.width/2;
              if (top >= 0 && left >= 0) {
                tooltipElem.style.top = `${top}px`
                tooltipElem.style.left = `${left}px`;
                break;
              };
            };
          };
        };
      };
      tooltipDispTime = performance.now(); // 
    };
