              detail.num = ++num;
              result.expectedSum += detail.expected; // 
              result.average += detail.experience - _this.mcDollExperience; // 
              medianList.push(detail.experience - _this.mcDollExperience); // 
              
              // 1515
              if (detail.experience >= _this.MC_EXPERIENCE_DOLL[_this.mcDollId].exp*15) {
                result.probabilityMaximum += detail.probability;
