              };
            };
            if (amariStr[i] == "") amariStr[i] = "";
          };
          result.note = ""
                      + "<br>R" + amariStr[0]
                      + "<br>SR" + amariStr[1]
                      + "<br>SSR" + amariStr[2]
                      + "<br><br>";
        });
