        };
      });
      //_this.$cookies.set('tsTalkNikkes', buf, 0);
      localStorage.setItem('tsTalkNikkes', buf);
    },

    mSmDownload() { // 
      html2canvas(document.getElementById('smGraphGrid')).then(canvas => {
        var result = document.querySelector("#smDownload");
        result.innerHTML = '';
        result.appendChild(canvas)

        let link = document.createElement("a");
        link.href = canvas.toDataURL("image/png");
        link.download = "nikke-scatter.png";
        link.click();
      });
    },

    mSelectTab(idx) {
      //this.$cookies.set('coSelectTab', idx, 0);
      localStorage.setItem('coSelectTab', idx);
      this.coSelTab = idx;
      this.mReset(); // 
      this.mClearQfFil(); // 
    },

    mLsPackCheck(id, chkOn) { // 
      let _this = this;
      this.packs.forEach(function(pack){
        if(pack.id == id) {
          pack.check = pack.check * (-1);

          if (chkOn) {
            if (_this.selected[pack.id] === undefined) {
              _this.selected[pack.id] = true;
            } else {
              _this.selected[pack.id] = !_this.selected[pack.id];
            }
          }
        }
      })
      this.mCalcLeveling();
    },

    mCalcLeveling() { // 
      let _this = this;

      this.needs[0].NextLv = this.coNikkeLv + 1;
      this.needs[1].NextLv = Math.floor(this.coNikkeLv / 20) * 20 + 21;
      this.needs[2].NextLv = this.needs[1].NextLv + 20;

      // 
      let creditDayNum = 0;
      let creditWeekNum = 0;
      let creditMonthNum = 0;
      let creditDayCase = 0;
      let creditWeekCase = 0;
      let creditMonthCase = 0;
      let coreDayNum = 0;
      let coreWeekNum = 0;
      let coreMonthNum = 0;
      let coreDayCase = 0;
      let coreWeekCase = 0;
      let coreMonthCase = 0;
      let battleDayNum = 0;
      let battleWeekNum = 0;
      let battleMonthNum = 0;
      let battleDayCase = 0;
      let battleWeekCase = 0;
      let battleMonthCase = 0;
      this.packs.forEach(function(pack){
        if (pack.check == true) {
          if (pack.division == "") {
            creditDayCase += pack.items.CaseCredit;
            creditDayNum += pack.items.Credit * 1000;
            coreDayCase += pack.items.CaseCore;
            coreDayNum += pack.items.Core;
            battleDayCase += pack.items.CaseBattle;
            battleDayNum += pack.items.Battle * 1000;
          } else if (pack.division == "") {
            creditWeekCase += pack.items.CaseCredit;
            creditWeekNum += pack.items.Credit * 1000;
            coreWeekCase += pack.items.CaseCore;
            coreWeekNum += pack.items.Core;
            battleWeekCase += pack.items.CaseBattle;
            battleWeekNum += pack.items.Battle * 1000;
          } else if (pack.division == "") {
            creditMonthCase += pack.items.CaseCredit;
            creditMonthNum += pack.items.Credit * 1000;
            coreMonthCase += pack.items.CaseCore;
            coreMonthNum += pack.items.Core;
            battleMonthCase += pack.items.CaseBattle;
            battleMonthNum += pack.items.Battle * 1000;
          } else if (pack.name == "30-DAY") {
            coreDayCase += 10;
          }
        }
      });

      // 
      var expNow = this.experience.find(e => e.Lv === this.coNikkeLv);

      for (let i=0; i <= 2; ++i) {
        if (this.needs[i].NextLv <= this.experience.length) {
          let expNext = this.experience.find(e => e.Lv === this.needs[i].NextLv);
          this.needs[i].NeedCredit = expNext.Credit - expNow.Credit - (this.lsPosCaseCredit * this.coRewardCredit) - (this.lsPosCredit * 1000);
          this.needs[i].NeedBattle = expNext.Battle - expNow.Battle - (this.lsPosCaseBattle * this.coRewardBattle) - (this.lsPosBattle * 1000);
          this.needs[i].NeedCore   = expNext.Core   - expNow.Core   - (this.lsPosCaseCore   * this.coRewardCore)   - this.lsPosCore;
        }
      }

      // 
      let flgCredit = false;
      let flgBattle = false;
      let flgCore = false;
      let lpCnt = -1;
      if (this.needs[0].NextLv <= this.experience.length) lpCnt++; // 
      if (this.needs[1].NextLv <= this.experience.length) lpCnt++; // 20
      if (this.needs[2].NextLv <= this.experience.length) lpCnt++; // 20

      for (let i = 0; i <= lpCnt; ++i) {
        let nextday = new Date();
        let nextdayStr = "";
        let preCredit = this.needs[i].NeedCredit;
        let preBattle = this.needs[i].NeedBattle;
        let preCore = this.needs[i].NeedCore;

        this.needs[i].HourCredit = 0;
        this.needs[i].HourBattle = 0;
        this.needs[i].HourCore = 0;
        this.needs[i].NeedYen = 0;
        
        this.needs[i].LogCredit = "";
        this.needs[i].LogCredit += "";
        this.needs[i].LogCredit += "<br>";
        this.needs[i].LogCredit += "<br>Lv" + this.coNikkeLv + "" + this.needs[i].NextLv + "" + Math.floor((this.needs[i].NeedCredit + (this.lsPosCaseCredit * this.coRewardCredit) + Number(this.lsPosCredit * 1000))/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>";
        this.needs[i].LogCredit += "<br>+" + Math.floor(((this.lsPosCaseCredit * this.coRewardCredit) + (this.lsPosCredit * 1000))/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>" + Math.floor(Math.floor(this.coRewardCredit*24)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "+" + Math.floor((Math.floor(this.lsWipeouts * this.coRewardCredit * 2) + (1 * Math.floor(this.coRewardCredit)))/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "" + Math.floor((creditDayCase * Math.floor(this.coRewardCredit) + creditDayNum)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>" + Math.floor(Math.floor((8 * this.coRewardCredit) * 2)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "" + Math.floor((18 * Math.floor(this.coRewardCredit) + 500000)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "" + Math.floor((creditWeekCase * Math.floor(this.coRewardCredit) + creditWeekNum)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>" + Math.floor((creditMonthCase * Math.floor(this.coRewardCredit) + creditMonthNum)/1000).toLocaleString() + "K";
        this.needs[i].LogCredit += "<br>";
        this.needs[i].LogCredit += "<br>" + nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "(" + [ "", "", "", "", "", "", "" ][nextday.getDay()] + ")";
        this.needs[i].LogCredit += "" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K";

        this.needs[i].LogBattle = "";
        this.needs[i].LogBattle += "";
        this.needs[i].LogBattle += "<br>";
        this.needs[i].LogBattle += "<br>Lv" + this.coNikkeLv + "" + this.needs[i].NextLv + "" + Math.floor((this.needs[i].NeedBattle + (this.lsPosCaseBattle * this.coRewardBattle) + Number(this.lsPosBattle * 1000))/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>";
        this.needs[i].LogBattle += "<br>+" + Math.floor(((this.lsPosCaseBattle * this.coRewardBattle) + (this.lsPosBattle * 1000))/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>" + Math.floor(this.coRewardBattle*24/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "+" + Math.floor((Math.floor(this.lsWipeouts * this.coRewardBattle * 2) + (18 * Math.floor(this.coRewardBattle)))/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "" + Math.floor((battleDayCase * Math.floor(this.coRewardBattle) + battleDayNum)/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>" + Math.floor((36 * Math.floor(this.coRewardBattle))/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "" + Math.floor((battleMonthCase * Math.floor(this.coRewardBattle) + battleMonthNum)/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>" + Math.floor((battleMonthCase * Math.floor(this.coRewardBattle) + battleMonthNum)/1000).toLocaleString() + "K";
        this.needs[i].LogBattle += "<br>";
        this.needs[i].LogBattle += "<br>" + nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "(" + [ "", "", "", "", "", "", "" ][nextday.getDay()] + ")";
        this.needs[i].LogBattle += "" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K";

        this.needs[i].LogCore = "";
        this.needs[i].LogCore += "";
        this.needs[i].LogCore += "<br>";
        this.needs[i].LogCore += "<br>Lv" + this.coNikkeLv + "" + this.needs[i].NextLv + "" + Math.floor(this.needs[i].NeedCore + (this.lsPosCaseCore * this.coRewardCore) + Number(this.lsPosCore)).toLocaleString();
        this.needs[i].LogCore += "<br>";
        this.needs[i].LogCore += "<br>+" + Math.floor((this.lsPosCaseCore * this.coRewardCore) + this.lsPosCore).toLocaleString();
        this.needs[i].LogCore += "<br>" + Math.floor(this.coRewardCore*24).toLocaleString();
        this.needs[i].LogCore += "+" + (Math.floor(this.lsWipeouts * this.coRewardCore * 2) + 50).toLocaleString();
        this.needs[i].LogCore += "" + (coreDayCase * Math.floor(this.coRewardCore) + coreDayNum).toLocaleString();
        this.needs[i].LogCore += "<br>" + (36 * Math.floor(this.coRewardCore)).toLocaleString();
        this.needs[i].LogCore += "" + (coreWeekCase * Math.floor(this.coRewardCore) + coreWeekNum).toLocaleString();
        this.needs[i].LogCore += "<br>" + (coreMonthCase * Math.floor(this.coRewardCore) + coreMonthNum).toLocaleString();
        this.needs[i].LogCore += "<br>";
        this.needs[i].LogCore += "<br>" + nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "(" + [ "", "", "", "", "", "", "" ][nextday.getDay()] + ")";
        this.needs[i].LogCore += "" + Math.floor(this.needs[i].NeedCore).toLocaleString();

        while (this.needs[i].NeedCredit > 0 || this.needs[i].NeedBattle > 0 || this.needs[i].NeedCore > 0) {
          nextday.setHours(nextday.getHours() + 1); // +1h
          nextdayStr = nextday.getFullYear() + "/" + ("00"+(nextday.getMonth()+1)).slice(-2) + "/" + ("00"+nextday.getDate()).slice(-2) + " " + ("00"+nextday.getHours()).slice(-2) + "(" + [ "", "", "", "", "", "", "" ][nextday.getDay()] + ")"
          flgCredit = false;
          flgBattle = false;
          flgCore = false;

          // 
          if (this.needs[i].NeedCredit > 0) {
            flgCredit = true;
            this.needs[i].HourCredit++; // +1
            this.needs[i].NeedCredit -= this.coRewardCredit; // 1
            if (nextday.getHours() == 5) { // 5
              this.needs[i].NeedCredit -= (creditDayCase * Math.floor(this.coRewardCredit)) // ()
                                         + creditDayNum // ()
                                         + Math.floor(this.lsWipeouts * this.coRewardCredit * 2) // 
                                         + (1 * Math.floor(this.coRewardCredit)); // 
              this.needs[i].LogCredit += "<br>" + nextdayStr + "" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K" + Math.floor((this.needs[i].NeedCredit - preCredit)/1000).toLocaleString() + "K";
              preCredit = this.needs[i].NeedCredit;
            }
            if (nextday.getHours() == 5 && nextday.getDay() == 2 && this.needs[i].NeedCredit > 0) { // 5
              this.needs[i].NeedCredit -= (creditWeekCase * Math.floor(this.coRewardCredit)) // ()
                                        + creditWeekNum // ()
                                        + Math.floor((8 * this.coRewardCredit)) * 2 // 
                                        + (18 * Math.floor(this.coRewardCredit) + 500000); // 
              this.needs[i].LogCredit += "<br>" + nextdayStr + "" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K" + Math.floor((this.needs[i].NeedCredit - preCredit)/1000).toLocaleString() + "K";
              preCredit = this.needs[i].NeedCredit;
            }
            if (nextday.getHours() == 5 && nextday.getDate() == 1 && this.needs[i].NeedCredit > 0) { // 5
              this.needs[i].NeedCredit -= (creditMonthCase * Math.floor(this.coRewardCredit)) // ()
                                        + creditMonthNum; // ()
              this.needs[i].LogCredit += "<br>" + nextdayStr + "" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K" + Math.floor((this.needs[i].NeedCredit - preCredit)/1000).toLocaleString() + "K";
              preCredit = this.needs[i].NeedCredit;
            }
            if (nextday.getHours() != 5 && this.needs[i].NeedCredit <= 0) {
              this.needs[i].LogCredit += "<br>" + nextdayStr + "" + Math.floor(this.needs[i].NeedCredit/1000).toLocaleString() + "K" + Math.floor((this.needs[i].NeedCredit - preCredit)/1000).toLocaleString() + "K";
            }
          }
          
          // 
          if (this.needs[i].NeedBattle > 0) {
            flgBattle = true;
            this.needs[i].HourBattle++; // +1
            this.needs[i].NeedBattle -= this.coRewardBattle; // 1
            if (nextday.getHours() == 5) { // 5
              this.needs[i].NeedBattle -= (battleDayCase * Math.floor(this.coRewardBattle)) // ()
                                        + battleDayNum // ()
                                        + Math.floor(this.lsWipeouts * this.coRewardBattle * 2) // 
                                        + (18 * Math.floor(this.coRewardBattle)); // 5-C
              this.needs[i].LogBattle += "<br>" + nextdayStr + "" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K" + Math.floor((this.needs[i].NeedBattle - preBattle)/1000).toLocaleString() + "K";
              preBattle = this.needs[i].NeedBattle;
            }
            if (nextday.getHours() == 5 && nextday.getDay() == 2 && this.needs[i].NeedBattle > 0) { // 5
              this.needs[i].NeedBattle -= (battleWeekCase * Math.floor(this.coRewardBattle)) // ()
                                        + battleWeekNum // ()
                                        + (36 * Math.floor(this.coRewardBattle)); // 
              this.needs[i].LogBattle += "<br>" + nextdayStr + "" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K" + Math.floor((this.needs[i].NeedBattle - preBattle)/1000).toLocaleString() + "K";
              preBattle = this.needs[i].NeedBattle;
            }
            if (nextday.getHours() == 5 && nextday.getDate() == 1 && this.needs[i].NeedBattle > 0) { // 5
              this.needs[i].NeedBattle -= (battleMonthCase * Math.floor(this.coRewardBattle)) // ()
                                        + battleMonthNum; // ()
              this.needs[i].LogBattle += "<br>" + nextdayStr + "" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K" + Math.floor((this.needs[i].NeedBattle - preBattle)/1000).toLocaleString() + "K";
              preBattle = this.needs[i].NeedBattle;
            }
            if (nextday.getHours() != 5 && this.needs[i].NeedBattle <= 0) {
              this.needs[i].LogBattle += "<br>" + nextdayStr + "" + Math.floor(this.needs[i].NeedBattle/1000).toLocaleString() + "K" + Math.floor((this.needs[i].NeedBattle - preBattle)/1000).toLocaleString() + "K";
            }
          }
          
          // 
          if (this.needs[i].NeedCore > 0) {
            flgCore = true;
            this.needs[i].HourCore++; // +1
            this.needs[i].NeedCore -= this.coRewardCore; // 1
            if (nextday.getHours() == 5) { // 5
              let pre = this.needs[i].NeedCore;
              this.needs[i].NeedCore -= (coreDayCase * Math.floor(this.coRewardCore)) // ()
                                      + coreDayNum // ()
                                      + Math.floor(this.lsWipeouts * this.coRewardCore * 2) // 
                                      + 50; // 
              this.needs[i].LogCore += "<br>" + nextdayStr + "" + Math.floor(this.needs[i].NeedCore).toLocaleString() + "" + Math.floor(this.needs[i].NeedCore - preCore).toLocaleString() + "";
              preCore = this.needs[i].NeedCore;
            }
            if (nextday.getHours() == 5 && nextday.getDay() == 2 && this.needs[i].NeedCore > 0) { // 5
              let pre = this.needs[i].NeedCore;
              this.needs[i].NeedCore -= (coreWeekCase * Math.floor(this.coRewardCore)) // ()
                                      + coreWeekNum // ()
                                      + (36 * Math.floor(this.coRewardCore)) // 
              this.needs[i].LogCore += "<br>" + nextdayStr + "" + Math.floor(this.needs[i].NeedCore).toLocaleString() + "" + Math.floor(this.needs[i].NeedCore - preCore).toLocaleString() + "";
              preCore = this.needs[i].NeedCore;
            }
            if (nextday.getHours() == 5 && nextday.getDate() == 1 && this.needs[i].NeedCore > 0) { // 5
              let pre = this.needs[i].NeedCore;
              this.needs[i].NeedCore -= (coreMonthCase * Math.floor(this.coRewardCore)) // ()
                                      + coreMonthNum; // ()
              this.needs[i].LogCore += "<br>" + nextdayStr + "" + Math.floor(this.needs[i].NeedCore).toLocaleString() + "" + Math.floor(this.needs[i].NeedCore - preCore).toLocaleString() + "";
              preCore = this.needs[i].NeedCore;
            }
            if (nextday.getHours() != 5 && this.needs[i].NeedCore <= 0) {
              this.needs[i].LogCore += "<br>" + nextdayStr + "" + Math.floor(this.needs[i].NeedCore).toLocaleString() + "" + Math.floor(this.needs[i].NeedCore - preCore).toLocaleString() + "";
            }
          }

          // 
          if (nextday.getHours() == 5) {
            if (flgCredit || flgBattle || flgCore) {
              //if (this.needs[i].NeedCredit >= 0 && this.needs[i].NeedBattle >= 0 && this.needs[i].NeedCore >= 0) {
                this.packs.forEach(function(pack){
                  if (pack.check == true) {
                    // 5
                    if (nextday.getHours() == 5 && pack.division == "") {
                      if (flgCredit && (pack.items.CaseCredit >= 1 || pack.items.Credit)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgBattle && (pack.items.CaseBattle >= 1 || pack.items.Battle)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgCore && (pack.items.CaseCore >= 1 || pack.items.Core)) {
                        _this.needs[i].NeedYen += pack.price;
                      }
                    }

                    // 5
                    if (nextday.getHours() == 5 && nextday.getDay() == 2 && pack.division == "") {
                      if (flgCredit && (pack.items.CaseCredit >= 1 || pack.items.Credit)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgBattle && (pack.items.CaseBattle >= 1 || pack.items.Battle)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgCore && (pack.items.CaseCore >= 1 || pack.items.Core)) {
                        _this.needs[i].NeedYen += pack.price;
                      }
                    }
                    
                    // 5
                    if (nextday.getHours() == 5 && nextday.getDate() == 1 && pack.division == "") {
                      if (flgCredit && (pack.items.CaseCredit >= 1 || pack.items.Credit)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgBattle && (pack.items.CaseBattle >= 1 || pack.items.Battle)) {
                        _this.needs[i].NeedYen += pack.price;
                      } else if (flgCore && (pack.items.CaseCore >= 1 || pack.items.Core)) {
                        _this.needs[i].NeedYen += pack.price;
                      }
                    }
                    
                    // 5
                    if (nextday.getHours() == 5 && nextday.getDate() == 1 && pack.name == "30-DAY") {
                      if (flgCore) {
                        _this.needs[i].NeedYen += pack.price;
                      }
                    }
                  }
                })
              //}
            }
          }
        };

        // 
        this.needs[i].DayCredit = this.needs[i].HourCredit / 24;
        this.needs[i].DayBattle = this.needs[i].HourBattle / 24;
        this.needs[i].DayCore   = this.needs[i].HourCore / 24;

        // 
        this.needs[i].DayCreditPer = Math.floor(this.needs[i].DayCredit / (Math.max(this.needs[i].DayCredit, this.needs[i].DayBattle, this.needs[i].DayCore)*1.2) * 100)
        this.needs[i].DayBattlePer = Math.floor(this.needs[i].DayBattle / (Math.max(this.needs[i].DayCredit, this.needs[i].DayBattle, this.needs[i].DayCore)*1.2) * 100)
        this.needs[i].DayCorePer   = Math.floor(this.needs[i].DayCore   / (Math.max(this.needs[i].DayCredit, this.needs[i].DayBattle, this.needs[i].DayCore)*1.2) * 100)

        // 
        let today = new Date();
        let maxHour = Math.max(this.needs[i].HourCredit, this.needs[i].HourBattle, this.needs[i].HourCore);
        today.setHours(today.getHours() + maxHour);
        this.needs[i].ArrivalDate = (today.getFullYear()+"").slice(-2) + "/" + ("00"+(today.getMonth()+1)).slice(-2) + "/" + ("00"+today.getDate()).slice(-2) + "<br>(" + [ "", "", "", "", "", "", "" ][today.getDay()] + ")<br>" + ("00"+today.getHours()).slice(-2) + "";

        // 
        this.lsToday = new Date();
        this.lsToday = "" + (this.lsToday.getFullYear()+"").slice(-2) + "/" + ("00"+(this.lsToday.getMonth()+1)).slice(-2) + "/" + ("00"+this.lsToday.getDate()).slice(-2) + "(" + [ "", "", "", "", "", "", "" ][this.lsToday.getDay()] + ")" + ("00"+this.lsToday.getHours()).slice(-2) + "";
      }

      for (let i = lpCnt+1; i <= 2; ++i) {
        // 
        this.needs[i].HourCredit = 0;
        this.needs[i].HourBattle = 0;
        this.needs[i].HourCore = 0;
        this.needs[i].DayCredit = 0;
        this.needs[i].DayBattle = 0;
        this.needs[i].DayCore = 0;
        this.needs[i].DayCreditPer = 0;
        this.needs[i].DayBattlePer = 0;
        this.needs[i].DayCorePer = 0;
        this.needs[i].ArrivalDate = "<br>";
        this.needs[i].NeedYen = 0;
      }

    },

    mChangeNikkeLv() { // 
      if (this.coNikkeLv < 1) {
        this.coNikkeLv = 1;
      } else if (this.coNikkeLv > this.experience.length) {
        this.coNikkeLv = this.experience.length;
      } else {
        this.coNikkeLv = Math.floor(this.coNikkeLv);
      }
      localStorage.setItem('coNikkeLv', this.coNikkeLv);
      this.mCalcLeveling();
    },

    mChangeRewardLv() { // 
      if (this.coRewardLv < 1) {
        this.coRewardLv = 1;
      } else if (this.coRewardLv > this.reward.length) {
        this.coRewardLv = this.reward.length;
      } else {
        this.coRewardLv = Math.floor(this.coRewardLv);
      }
      var found = this.reward.find(e => e.Lv === this.coRewardLv);
      this.coRewardCredit = Math.floor(found.Credit*1.6);
      this.coRewardBattle = Math.floor(found.Battle*1.45);
      this.coRewardCore = Math.floor(found.Core*1.45 * Math.pow(10,2)) / Math.pow(10,2);
      localStorage.setItem('coRewardLv', this.coRewardLv);
      localStorage.setItem('coRewardCredit', this.coRewardCredit);
      localStorage.setItem('coRewardBattle', this.coRewardBattle);
      localStorage.setItem('coRewardCore', this.coRewardCore);
      this.mCalcLeveling();
    },

    mChangeWipeouts() { // 
      if (this.lsWipeouts < 1) {
        this.lsWipeouts = 1;
      } else if (this.lsWipeouts > 11) {
        this.lsWipeouts = 11;
      } else {
        this.lsWipeouts = Math.floor(this.lsWipeouts);
      }
      //this.$cookies.set('lsWipeouts', this.lsWipeouts, 0);
      localStorage.setItem('lsWipeouts', this.lsWipeouts);
